

import menuData from './menu'
import lipstickData from './lipstick'
import photoData from './photo'
import studentData from './student'

// 通过名字查找成绩
let studentDataByName = studentData.reduce((cur,item)=>{
    if(Array.isArray(cur[item.name])) {
        cur[item.name].push(item)
        return cur;
    }
    if(cur[item.name]){
        cur[item.name]=[cur[item.name],item]
    }else{
        cur[item.name] = item;
    }
    return cur;
},{})

// 通过班级 获取所有人
let studentDataByClass = studentData.reduce((cur,item)=>{
    let classNumber = item.classNumber.substring(0,3)
    if(cur[classNumber]){
        cur[classNumber].push(item);
    }else{
        cur[classNumber]=[];
        cur[classNumber].push(item)
    }
    return cur;
},{})

for(let key in studentDataByClass){
    let data = studentDataByClass[key].sort((a, b)=> b.totalScore - a.totalScore)
    let index = 0;
    for(let stu in data){
        index++
        data[stu]['classRanking'] = index;
    }
}

let studentDataByClassOrName = {...studentDataByName, ...studentDataByClass}

export {
    menuData,
    lipstickData,
    photoData,
    studentData,
    studentDataByName,
    studentDataByClass,
    studentDataByClassOrName,
}


